<template>
  <div id="types">
    <el-row>
      <el-col :span="16">
        <el-table :data="typeLists" border style="width: 100%">
          <el-table-column prop="code" label="编码" width="180">
          </el-table-column>
          <el-table-column prop="name" label="名称" width="180">
          </el-table-column>
          <el-table-column label="类型">
            <template slot-scope="scope">
              {{typeMap[scope.row.catalog]}}
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope" v-if="scope.row.catalog === 1">
              <el-button type="danger" size="small" plain @click="deleteType(scope.row)">删除</el-button>
              <el-button type="warning" size="small" plain @click="editType(scope.row)">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="type-btns">
          <el-button size="small" type="success" plain @click="showTypeEdit">新 增 类 型</el-button>
        </div>
      </el-col>
    </el-row>

    <el-dialog title="类型编辑" :visible.sync="editShow" width="50%">
      <div>
        <el-form ref="typeform" :model="typeData" :rules="rules" label-width="80px">
          <el-form-item label="编码" prop="code">
            <el-input v-model="typeData.code"></el-input>
          </el-form-item>
          <el-form-item label="名称" prop="name">
            <el-input v-model="typeData.name"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editShow = false">取 消</el-button>
        <el-button type="success" @click="save">保 存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'typeviews',
    props: ['grouptype'],
    data() {
      return {
        typeLists: [],
        typeMap: {
          0: '系统定义',
          1: '用户自建'
        },
        editShow: false,
        typeData: {},
        rules: {
          code: [{
            required: true,
            message: '请输入编码',
            trigger: 'blur'
          }, {
            min: 2,
            message: '最少输入2个字符',
            trigger: 'blur'
          }],
          name: [{
            required: true,
            message: '请输入名称',
            trigger: 'blur'
          }, {
            min: 2,
            message: '最少输入2个字符',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      getTypeLists() {
        this.$http.get(`/ec/api/types?type=${this.grouptype || 'budgets'}`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            this.typeLists = resData.data || [];
          }
        })
      },
      showTypeEdit() {
        this.typeData = {
          code: '',
          name: '',
          type: this.grouptype,
          catalog: 1,
        }
        this.editShow = true;
      },
      editType(typeData) {
        this.typeData = typeData;
        this.editShow = true;
      },
      deleteType(typeData) {
        this.typeData = typeData;
        this.$http({
          url: '/ec/api/types',
          method: 'DELETE',
          data: this.typeData
        }).then(res => {
          if (res.data.errcode === 0) {
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getTypeLists();
            this.editShow = false;
          }
        })
      },
      save() {
        this.$refs.typeform.validate(valid => {
          if (!valid) {
            this.$message({message: '请正确填写类型信息', type: 'warning'});
            return;
          }
          let method = 'POST';
          if (this.typeData._id) {
            method = 'PUT'
          }

          if(method === 'POST') {
            let index = this.typeLists.findIndex(item => item.code === this.typeData.code)
            if(index > -1) {
              return this.$message({message: `已经存在编码为 ${this.typeData.code}的类型`})
            }
          } else {
            let index = this.typeLists.findIndex(item => {
              return (item.code === this.typeData.code) && item._id !== this.typeData._id;
            })
            if(index > -1) {
              return this.$message({message: `已经存在编码为 ${this.typeData.code}的类型`})
            }
          }

          this.$http({
            url: '/ec/api/types',
            method,
            data: this.typeData
          }).then(res => {
            if (res.data.errcode === 0) {
              this.$message({
                message: '保存成功',
                type: 'success'
              });
              this.getTypeLists();
              this.editShow = false;
            }
          })
        })
      },
    },
    created() {
      this.getTypeLists();
    }
  }
</script>

<style>
  .type-btns {
    margin-top: 20px;
    text-align: right;
  }
</style>